@charset "UTF-8";
/* Default styles */
@import 'google-fonts.css';
:root {
  --yale-blue: hsla(212, 72%, 26%, 1);
  --berkeley-blue: hsla(215, 66%, 22%, 1);
  --oxford-blue: hsla(213, 72%, 16%, 1);
  --powder-blue: hsla(209, 32%, 66%, 1);
  --mint-cream: hsla(111, 24%, 94%, 1);
  --phthalo-blue: hsla(243, 76%, 27%, 1);
  --black-pearl: hsla(230, 70%, 5%, 1);
  --lavender-web: hsla(248, 54%, 89%, 1);
  --moonstone: hsla(185, 84%, 38%, 1);
  --moonstone-dark: hsl(200, 84%, 38%);
  --russian-violet: hsla(295, 100%, 15%, 1);
  --chinese-violet: hsla(269, 9%, 45%, 1);
  --btn-grey: hsla(240, 3%, 12%, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-weight: normal;
  color: var(--mint-cream);
}

body {
  background: var(--black-pearl) url(../src/media/bg-004-1.webp) top center/cover no-repeat fixed;
  color: var(--mint-cream);
  min-height: 900px;
  display: grid;
  grid-template-rows: auto 1fr auto;
  transition: background 0.25s ease-in-out;
  overflow-x: clip;
}
@media (max-width: 640px) {
  body {
    background: var(--black-pearl) url(../src/media/bg-004-1.webp) top center/auto 100vh no-repeat fixed;
  }
}
@media (min-width: 1500px) {
  body {
    background: var(--black-pearl) url(../src/media/bg-004.webp) top center/cover no-repeat fixed;
  }
}

@font-face {
  font-family: "Pirulen";
  src: url("/fonts/Pirulen.ttf.woff") format("woff"), url("/fonts/Pirulen.ttf.svg#Pirulen") format("svg"), url("/fonts/Pirulen.ttf.eot"), url("/fonts/Pirulen.ttf.eot?#iefix") format("embedded-opentype");
  font-weight: normal;
  font-style: normal;
}
main, ::placeholder {
  font-family: "Poppins";
}

a, header, button {
  font-family: "League Spartan", sans-serif;
  font-weight: 400;
}

h1 {
  font-family: "Pirulen";
  font-weight: 700;
}

h2,
h3,
h4,
h5 {
  font-family: "Ubuntu";
  font-weight: 700;
  text-transform: uppercase;
}

html {
  font-size: 100%;
} /* 16px */
h1 {
  font-size: 1.65rem; /* 48.8px */
}

h2 {
  font-size: 1.25rem; /* 39.04px */
}

h3 {
  font-size: 1.954rem; /* 31.2px */
}

h4 {
  font-size: 1.563rem; /* 24.96px */
}

h5, a {
  font-size: 1.25rem; /* 20px */
}

small {
  font-size: 0.8rem; /* 12.8px */
}

header {
  max-width: 100vw;
}
@media (max-width: 1536px) {
  header a {
    font-size: 1rem;
  }
}

nav {
  display: flex;
  align-items: center;
  padding: 1em;
  gap: 1em;
  z-index: 3;
}
nav .logo {
  cursor: pointer;
  width: 50px;
  height: auto;
}
nav .logo img {
  width: 100%;
  height: 100%;
}

.nav-links {
  display: flex;
  width: 100%;
}
.nav-links .links {
  display: flex;
  height: max-content;
  gap: 1em;
  margin-left: auto;
}
.nav-links .links > :nth-child(1) {
  position: relative;
}
.nav-links .links > :nth-child(2) {
  position: relative;
  padding-left: 32px;
}
.nav-links .links > :nth-child(2)::after {
  position: absolute;
  content: "";
  top: 45%;
  transform: translateY(-50%) scale(0.5);
  left: -5px;
  height: 200%;
  width: 4px;
  border-radius: 2px;
  background: var(--lavender-web);
  z-index: 5;
}

.menu a {
  position: relative;
  z-index: 5;
}

#nav-menu {
  position: absolute;
  top: 24px;
  right: 16px;
  width: 25px;
  height: 25px;
  opacity: 0;
  z-index: 6;
}
#nav-menu:checked ~ .burger > :nth-child(1) {
  transform: rotate(45deg) translate(7px, 2px);
}
#nav-menu:checked ~ .burger > :nth-child(2) {
  opacity: 0;
}
#nav-menu:checked ~ .burger > :nth-child(3) {
  transform: rotate(-45deg) translate(7px, -2px);
}
#nav-menu:checked ~ .burger-links {
  height: max-content;
  transform: scale(1);
}
#nav-menu:checked ~ label {
  display: block;
}

.burger {
  position: relative;
  z-index: 5;
}
.burger div {
  width: 25px;
  height: 2px;
  border-radius: 2px;
  margin: 4px 0;
  background: var(--mint-cream);
  transition: transform 0.25s ease-in-out, opacity 0.25s ease;
}
.burger.toggle {
  position: absolute;
  right: 16px;
  top: 24px;
}

.burger-links {
  display: flex;
  gap: 1.5em;
  position: absolute;
  top: 58px;
  right: 8px;
  flex-direction: column;
  height: 1px;
  transform: scale(0);
  transform-origin: top right;
  overflow: hidden;
  width: 250px;
  background: hsla(230, 70%, 5%, 0.75);
  border: 2px solid hsla(111, 24%, 94%, 0.1);
  backdrop-filter: blur(2px);
  border-radius: 1em 5px 1em 1em;
  padding: 1em;
  z-index: 4;
  transition: height 0.35s ease-in-out 0.25s, transform 0.25s ease-in-out;
}
.burger-links:hover .link {
  opacity: 0.5;
}
.burger-links .link {
  display: flex;
  align-items: center;
  gap: 8px;
  transition: 0.15s ease-in-out;
}
.burger-links .link a {
  font-size: 1rem;
}
.burger-links .link img {
  filter: invert(100%);
}
.burger-links .link:hover {
  opacity: 1;
}

.command {
  border-radius: 1.75em;
  background: hsla(230, 70%, 5%, 0.75) url("/media/nav-001.webp") 50% 85%/500px no-repeat;
  border: 2px solid hsla(111, 24%, 94%, 0.5);
  backdrop-filter: blur(2px);
  padding: 10px 1em 8px 1em;
  font-size: 1rem;
  font-weight: 400;
  transition: 0.5s ease-in-out border, background 0.5s ease-in;
}
.command:hover {
  background: hsla(230, 70%, 5%, 0.75) url("/media/nav-001.webp") 50% 85%/475px no-repeat;
  border: 2px solid hsl(111, 24%, 94%);
}

.toogle-menu {
  display: none;
  cursor: initial;
}
.toogle-menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3;
}

footer {
  max-width: 100vw;
  text-align: left;
  line-height: 25px;
  padding: 0 0 1em;
  margin-top: 6em;
  position: relative;
  font-family: "Poppins";
  background: hsla(0, 0%, 0%, 0.5);
  backdrop-filter: blur(2px);
  box-shadow: -4px 0px 5px #000, -4px 0px 20px #000, -6px 0px 30px #000, -6px 0px 15px #000 inset, 6px 0px 20px #000 inset;
}
footer a {
  font-size: 1rem;
}
@media (max-width: 1280px) {
  footer .deco.ship {
    top: -25px;
  }
  footer .deco.spatio {
    width: 250px;
  }
}
@media (max-width: 1024px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: initial;
    bottom: 0;
    width: 125px;
  }
  footer .deco.spatio {
    width: 150px;
  }
  footer .get-in-touch {
    max-width: 80%;
  }
  footer .copyright {
    font-weight: 200;
    font-size: 0.9rem;
  }
}
@media (max-width: 768px) {
  footer .get-in-touch {
    max-width: 90%;
  }
}
@media (max-width: 640px) {
  footer .get-in-touch  {
    flex-direction: column;
    text-align: center;
    gap: 1em;
  }
  footer .adress {
    order: 1;
  }
  footer .tel {
    order: 2;
  }
  footer .mail {
    order: 3;
  }
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 12px;
    width: 150px;
  }
  footer .deco.spatio {
    right: 24px;
    width: 150px;
  }
}
@media (max-width: 475px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 1em;
    width: 100px;
  }
  footer .deco.spatio {
    top: -15%;
    right: 1em;
    width: 100px;
  }
}

.separation {
  height: 2px;
  border-radius: 4px;
  width: 50%;
  min-width: 200px;
  margin: 0 auto calc(3em + 12px);
  background: var(--lavender-web);
  position: relative;
  top: -1px;
}

.get-in-touch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 60%;
  min-width: 280px;
  margin: 0 auto;
  text-transform: uppercase;
}
.get-in-touch a {
  font-weight: 200;
}
.get-in-touch ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 1em;
}
.get-in-touch li {
  width: max-content;
}
.get-in-touch .adress {
  pointer-events: none;
}

.social-media {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 2em 0 1em;
}

.ico {
  transition: transform ease-in-out 0.25s;
  cursor: pointer;
}
.ico:hover {
  transform: scale(1.1) translateY(-5px);
}

.copyright {
  text-align: center;
}

.deco {
  position: absolute;
  top: -75px;
  height: auto;
  pointer-events: none;
  z-index: 1;
}
.deco img {
  width: 100%;
  height: 100%;
}
.deco.ship {
  left: clamp(5px, 10%, 50px);
  top: -25px;
  width: 200px;
  animation: flying 1.5s ease infinite;
}
.deco.spatio {
  right: clamp(5px, 10%, 50px);
  width: 200px;
  animation: floating 5s ease infinite;
}

.get-to-top {
  position: absolute;
  top: -115px;
  right: 1em;
  filter: invert(100%);
  opacity: 0.7;
}

#home-1 {
  min-height: calc(100vh - 86px);
  position: relative;
  display: flex;
  align-items: center;
}
#home-1 .content {
  width: 55%;
  height: max-content;
  line-height: 27px;
  text-align: justify;
}
#home-1 h1 {
  font-size: 1.753rem;
}
#home-1 h2 {
  margin: 8px 0 0;
}
#home-1 .text {
  margin: 1em 0 0;
}

.deco-home {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: auto;
  height: clamp(250px, 60vh, 600px);
  opacity: 0.7;
  pointer-events: none;
}
.deco-home img {
  height: 100%;
  width: 100%;
}
.deco-home.spatios {
  right: 20%;
  opacity: 1;
  animation: floating-home 7s ease infinite;
}
.deco-home.planets {
  z-index: -1;
}
.deco-home.planets.two {
  left: 0;
  top: 70%;
  aspect-ratio: 1/1;
  scale: 0.8;
}

#home-2 {
  min-height: 80vh;
  display: grid;
  align-content: center;
}
#home-2 .content {
  display: flex;
  align-items: center;
  justify-content: center;
}
#home-2 .tech {
  width: 70%;
}
#home-2 h2 {
  font-family: "Pirulen";
  text-align: center;
  margin-bottom: 1em;
}

.tools {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 1.5em;
  max-width: 70%;
  border: 2px solid var(--lavender-web);
  border-radius: 1.75em;
  padding: 1.75em;
  margin: 0 auto;
  background: hsla(230, 70%, 5%, 0.5);
  backdrop-filter: blur(2px);
}

.fact {
  width: 30%;
}

.fact-box {
  height: 305px;
  position: relative;
  border: 2px solid var(--lavender-web);
  background: hsla(230, 70%, 5%, 0.5);
  backdrop-filter: blur(2px);
  border-radius: 1.75em;
  padding: 1.75em 1.75em 2.5em;
  width: max-content;
  margin: 0 auto;
}
.fact-box::after {
  content: "";
  position: absolute;
  top: -3px;
  left: 50%;
  transform: translateX(-50%);
  height: 4px;
  width: 85%;
  background: var(--black-pearl);
}
.fact-box h2 {
  font-size: 1rem;
  font-family: "Pirulen";
  width: max-content;
  text-align: center;
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

.slider {
  width: 300px;
  gap: 1em;
  text-align: justify;
}
.slider h3 {
  text-align: center;
  font-size: 1.2rem;
  margin: 0 0 12px;
}

.pagination {
  display: flex;
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
}
.pagination > div {
  width: 10px;
  margin: 0 4px;
  aspect-ratio: 1/1;
  background: var(--lavender-web);
  border-radius: 50%;
}
.pagination > div.active {
  height: 10px;
  width: 20px;
  border-radius: 16px;
}

[slide-step] > .wrap {
  display: none;
}

[slide-step="1"] > .slides-1,
[slide-step="2"] > .slides-2,
[slide-step="3"] > .slides-3,
[slide-step="4"] > .slides-4,
[slide-step="5"] > .slides-5,
[slide-step="6"] > .slides-6,
[slide-step="7"] > .slides-7,
[slide-step="8"] > .slides-8,
[slide-step="9"] > .slides-9,
[slide-step="10"] > .slides-10,
[slide-step="11"] > .slides-11 {
  display: block;
}

.wrap {
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(15px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
#home-3 {
  min-height: 100vh;
  display: grid;
  align-content: center;
}
#home-3 .content {
  display: flex;
}
#home-3 .portfolio {
  text-align: center;
  width: 65%;
  margin: 0 auto;
}
#home-3 .portfolio h2 {
  margin: 0 0 1em;
  font-family: "Pirulen";
  letter-spacing: 1px;
  position: relative;
  top: -45px;
}
#home-3 .portfolios {
  background: hsla(230, 70%, 5%, 0.31) url("/media/home/bg-port.webp") top center/cover no-repeat;
  border-radius: 2em;
  border: 2px solid hsla(111, 24%, 94%, 0.1);
  display: flex;
  gap: 2.5em;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0 auto;
  padding: 4.5em 0;
  position: relative;
  top: -45px;
}
#home-3 .portfolios a {
  display: block;
  width: 275px;
  height: max-content;
  border-radius: 2em;
  border: 2px solid #fff;
  background: hsla(230, 70%, 5%, 0.7);
  box-shadow: 0px 0px 10px var(--moonstone-dark), 0px 0px 10px var(--moonstone-dark) inset;
  padding: 1em;
  max-width: 27ch;
  text-align: center;
  letter-spacing: 1px;
}
#home-3 .home-links {
  width: max-content;
  margin: 0 auto;
  border-radius: 2em;
  border: 2px solid hsla(111, 24%, 94%, 0.1);
  background: hsla(230, 70%, 5%, 0.7);
  backdrop-filter: blur(2px);
  padding: 2em;
  display: flex;
  flex-direction: column;
  position: relative;
}
#home-3 .home-links a {
  border-radius: 2em;
  border: 2px solid #fff;
  box-shadow: 0px 0px 10px var(--mint-cream), 0px 0px 10px var(--mint-cream) inset;
  padding: 1em;
  margin: 12px 1em;
  max-width: 25ch;
  text-align: center;
}
#home-3 .home-links .spatio-home {
  position: absolute;
  bottom: -5px;
  left: -75px;
  height: 350px;
  width: auto;
}
#home-3 .home-links .spatio-home img {
  width: 100%;
  height: 100%;
}

[active-step] > div {
  display: none;
}

[active-step="1"] > .step-1,
[active-step="2"] > .step-2,
[active-step="3"] > .step-3,
[active-step="4"] > .step-4,
[active-step="5"] > .step-5 {
  display: block;
}

[type=radio], [type=checkbox], label {
  cursor: pointer;
}

.info {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#order {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 86px);
}
#order h1 {
  text-align: center;
  margin-bottom: 1em;
}

input:not([type=checkbox], [type=radio]) {
  border: 2px solid var(--yale-blue);
  background: none;
  padding: 8px 16px;
  width: clamp(250px, 27vw, 350px);
  border-radius: 1.75em;
  margin: 1em;
  position: relative;
}
input:not([type=checkbox], [type=radio]):focus {
  outline: none;
  border-color: var(--moonstone);
}

.inputs {
  position: relative;
}
.inputs input:not([type=checkbox], [type=radio]) {
  color: var(--lavender-web);
  font-family: "League Spartan";
  font-weight: 300;
  font-size: 1.1rem;
}
.inputs input:not([type=checkbox], [type=radio]):focus + label {
  color: var(--moonstone);
}
.inputs input:not([type=checkbox], [type=radio]):placeholder-shown + label {
  top: 50%;
  transform: translateY(-50%);
  left: 34px;
  color: grey;
  padding: 0;
  background: none;
}
.inputs label {
  position: absolute;
  top: 4px;
  font-size: 0.95em;
  left: 2.5em;
  z-index: 2;
  color: var(--yale-blue);
  background: var(--black-pearl);
  padding: 0 8px;
  transition: top 0.15s ease-in, left 0.15s ease-in, color 0.15s ease-in, border 0.15s ease-in, background 0.15s ease-in, transform 0.15s ease-in, padding 0.15s ease-in;
  pointer-events: none;
}

.in {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 475px) {
  .in {
    justify-content: flex-start;
  }
}

form {
  background: var(--black-pearl);
  margin: 0 auto;
  padding: 1em 1em 3em;
  border-radius: 1.75em;
  border: 2px solid var(--mint-cream);
  min-height: 375px;
  height: 100%;
  width: clamp(250px, 65%, 1000px);
  text-align: center;
  position: relative;
  display: grid;
  align-content: center;
}
form h2 {
  margin-bottom: 1em;
}
form .buttons {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1em;
}
@media (max-width: 640px) {
  form {
    width: clamp(250px, 85%, 1000px);
  }
  form h2 {
    font-size: 1.25em;
    margin: 12px 0 0;
  }
}

button {
  background: var(--btn-grey);
  border: none;
  border-radius: 1em;
  padding: 1em 3em;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  box-shadow: -4px -4px 3px hsla(0, 0%, 20%, 0.521);
}

.option {
  display: flex;
  width: max-content;
}
@media (max-width: 475px) {
  .option {
    margin-left: 1em;
  }
}

.check, .option {
  line-height: 30px;
}
.check input, .option input {
  opacity: 0;
}
.check input:checked + label::after, .option input:checked + label::after {
  background: var(--black-pearl) url(/media/nav/check.svg) center/14px no-repeat;
  left: -5px;
}
.check input:checked + label::before, .option input:checked + label::before {
  background: var(--black-pearl);
}
.check label, .option label {
  padding-left: 1.5em;
  cursor: pointer;
  position: relative;
}
.check label::after, .option label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.8);
  border-radius: 50%;
  border: 2px solid var(--yale-blue);
  left: -20px;
  width: 20px;
  aspect-ratio: 1/1;
  background: var(--mint-cream) url(/media/nav/check.svg) center/0px no-repeat;
  transition: 0.25s all ease-in-out;
}
.check label::before, .option label::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.7);
  border: 2px solid var(--yale-blue);
  left: -20px;
  width: 34px;
  height: 20px;
  border-radius: 10px;
  background: var(--mint-cream);
  opacity: 0.7;
  transition: 0.25s all ease-in-out;
}

.check.second {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1em;
}
@media (max-width: 640px) {
  .check.second {
    grid-template-columns: 1fr;
    gap: 4px;
  }
}

.more {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3em 1em;
  width: 80%;
  margin: 0 auto;
  text-align: left;
}
.more h3 {
  font-size: 1.2rem;
  font-weight: 400;
  opacity: 0.7;
}

.step-4 textarea {
  font-family: "Poppins";
  font-weight: 300;
  letter-spacing: 1px;
  width: 100%;
  background: none;
  border: 2px solid var(--yale-blue);
  border-radius: 1em;
  margin: 1em 0 0;
  padding: 1em;
}
.step-4 textarea:focus {
  outline: none;
  border: 2px solid var(--moonstone);
}

.step-5 h2 {
  margin: 0 0 8px;
}

.parent label {
  width: max-content;
  padding: 0 1em 0 1.75em;
  position: relative;
}
.parent label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.8);
  border-radius: 50%;
  border: 2px solid var(--yale-blue);
  left: 2px;
  width: 20px;
  aspect-ratio: 1/1;
  background: var(--mint-cream) url(/media/nav/check.svg) center/0px no-repeat;
  transition: 0.25s all ease-in-out;
}
.parent label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid var(--yale-blue);
  border-radius: 1em;
  width: 100%;
  height: 100%;
  transition: 0.25s all ease-in-out;
  opacity: 0;
}
.parent input:checked + label {
  color: var(--yale-blue);
}
.parent input:checked + label::after {
  background: var(--black-pearl) url(/media/nav/check.svg) center/14px no-repeat;
}
.parent input:checked + label::before {
  opacity: 1;
}

.radio {
  display: flex;
  gap: 1.75em;
  justify-content: center;
  margin: 0 auto 2.5em;
}
.radio > div {
  display: flex;
  gap: 4px;
}
.radio input {
  opacity: 0;
}

.confidentiality {
  width: max-content;
  margin: 0 auto;
}
.confidentiality > :first-child {
  background: var(--yale-blue);
  width: max-content;
  margin: 0 auto;
  padding: 6px 12px;
  border-radius: 1.75em;
  cursor: pointer;
}

#offer h1 {
  font-size: 1.4rem;
  text-align: center;
  width: 75%;
  margin: 1em auto 1.75em;
}
#offer h2 {
  opacity: 0.8;
  margin: 0 0 1em;
}

.offer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3em;
  justify-content: space-evenly;
  background: hsla(230, 70%, 5%, 0.5);
  backdrop-filter: blur(3px);
  border: 2px solid #3f3636;
  border-radius: 1.5em;
  padding: 1.75em;
}

.extras {
  text-align: center;
  grid-column: span 2;
}
.extras .list {
  grid-template-columns: 1fr 1fr 1fr;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1em;
  margin-top: 12px;
}

.product {
  border: 2px solid var(--oxford-blue);
  background: var(--black-pearl);
  backdrop-filter: blur(25%);
  border-radius: 1.5em;
  padding: 8px;
  outline: 1px solid hsla(0, 0%, 8%, 0.3);
  outline-offset: 3px;
  text-align: center;
}

.ord {
  text-align: center;
  width: max-content;
  margin: 3em auto;
}
.ord h1 {
  font-size: 1.2rem;
  margin: 0 0 1.75em;
}
.ord a {
  background: var(--btn-grey);
  border: none;
  border-radius: 1em;
  padding: 1em 3em;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  box-shadow: -4px -4px 3px hsla(0, 0%, 20%, 0.521);
}

#works {
  min-height: calc(100vh - 86px);
}
#works h1 {
  margin: 1.5em 0 2em;
  text-align: center;
}

.site {
  position: relative;
  margin: 0 0 75px;
}

.responsive {
  position: relative;
  max-width: 800px;
  min-width: 300px;
  margin: 0 auto;
  height: auto;
}
.responsive img {
  width: 100%;
  height: 100%;
}

.descrip {
  display: flex;
  position: relative;
  width: max-content;
  margin: 1.75em auto 0;
  line-height: 20px;
  text-align: center;
}
.descrip h2 {
  margin: 0 0 8px;
  text-align: initial;
  width: max-content;
  max-width: none;
}
.descrip::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
  height: 4px;
  width: 75%;
  border-radius: 2px;
  background: hsla(223, 9%, 45%, 0.459);
}

.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  filter: invert(100%);
  opacity: 0.7;
  transition: 0.25s opacity ease-in-out;
  cursor: pointer;
}
.nav-btn:hover {
  opacity: 1;
}
.nav-btn.next {
  right: 0;
  transform: rotate(90deg);
}
.nav-btn.pre {
  left: 0;
  transform: rotate(-90deg);
}

[active-site] > div {
  display: none;
}

[active-site="1"] > .site-1,
[active-site="2"] > .site-2 {
  display: initial;
}

.prev, .descrip {
  animation: siteIn 0.5s ease-out;
}

@keyframes siteIn {
  from {
    transform: translateX(25px);
  }
  to {
    transform: translateX(0);
  }
}
#pubs {
  min-height: calc(100vh - 86px);
  width: 100%;
  margin-bottom: 3em;
}
#pubs h1 {
  margin: 1.75em 0 3em;
  text-align: center;
}
@media (max-width: 640px) {
  #pubs {
    overflow-x: clip;
  }
}

.card-swiper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 150px 100px;
  justify-content: center;
  align-items: center;
  width: 100%;
}
@media (max-width: 640px) {
  .card-swiper {
    flex-direction: column;
  }
}
.card-swiper p {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: max-content;
  bottom: -12%;
  background: var(--btn-grey);
  border: none;
  border-radius: 1em;
  padding: 1em 3em;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  box-shadow: -4px -4px 3px hsla(0, 0%, 20%, 0.521);
  padding: 1em 1.75em;
}

.card-groups,
.card-group,
.card {
  aspect-ratio: 5/7;
}

.card-groups,
.card-group,
.big-card {
  width: 30vmin;
  margin: 0 auto;
  min-width: 200px;
}

.card-group {
  position: relative;
  transition: transform 400ms ease;
}

.card {
  background-color: rgba(255, 255, 255, 0.05);
  position: absolute;
  transition: transform 800ms cubic-bezier(0.05, 0.43, 0.25, 0.95);
  background-position: top center;
  background-size: cover;
}

.big-card {
  border-radius: 1vmin;
}

.little-card {
  width: 12vmin;
  border-radius: 2vmin;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: -1vmin 1vmin 2vmin rgba(0, 0, 0, 0.25);
  pointer-events: none;
