#about {
  margin-top: clamp(25px, 10%, 75px);
  overflow: hidden;
  width: 100vw;
}
#about > :first-child {
  background: hsla(230, 70%, 5%, 0.7);
  border-radius: 1.5em;
  padding: 3em;
  margin-bottom: 100px;
  position: relative;
  z-index: 1;
}
#about .spatios {
  opacity: 0.6;
}
#about .planets.one {
  top: 60%;
  right: -5%;
  scale: 125%;
}
@media (max-width: 1024px) {
  #about h1 {
    font-size: clamp(1.2rem, 3vw, 3rem);
  }
  #about .planets.one {
    top: 45%;
    right: -7%;
    scale: 100%;
  }
  #about .part {
    width: 90%;
    padding: 2.5em;
  }
}
@media (max-width: 768px) {
  #about .planets.two {
    top: 110%;
    left: -10%;
  }
}
@media (max-width: 640px) {
  #about .part {
    width: 100%;
    padding: 1.5em;
  }
  #about .spatios {
    opacity: 0.5;
    scale: 0.7;
  }
  #about .planets.one {
    top: 45%;
    right: -7%;
    scale: 80%;
    opacity: 0.3;
  }
  #about .planets.two {
    top: 130%;
    right: -7%;
    scale: 75%;
    opacity: 0.3;
  }
}
@media (max-width: 475px) {
  #about.container {
    padding: 1em;
    font-size: 0.9rem;
  }
  #about .part {
    padding: 1.5em;
  }
}

.part {
  margin: 0 auto 3em;
  width: 80%;
  min-width: none;
  text-align: justify;
}
.part h1 {
  margin-bottom: 1em;
  text-align: center;
}

.team {
  margin-top: 1.75em;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3em;
}

.person {
  width: max-content;
  text-align: center;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  font-size: 1.2rem;
}
.person h4 {
  font-size: 1.2rem;
  font-weight: 600;
}
.person .picture {
  text-align: center;
  height: 200px;
  aspect-ratio: 1/1;
  margin: 0 auto 8px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--oxford-blue);
  box-shadow: 0px 0px 10px var(--chinese-violet);
}
.person .picture img {
  width: 125%;
  height: 125%;
  object-fit: cover;
  object-position: -170px 0;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (min-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
@keyframes floating {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, 8px);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes floating-home {
  0% {
    transform: translateY(-49%) rotate(2deg);
  }
  50% {
    transform: translateY(-50%) rotate(0);
  }
  100% {
    transform: translateY(-49%) rotate(2deg);
  }
}
@keyframes flying {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(4deg);
  }
  100% {
    transform: rotate(0);
  }
}